.skills {
    padding: 80px 0;
    background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
    margin-bottom: 4rem;
    justify-items: center;
}

.skill-category {
    background: rgba(255, 255, 255, 0.9);
    padding: 2rem;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    text-align: center;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.skills-grid > .skill-category {
    width: 100%;
}

/* First row - span 2 columns each to fill the 6-column grid */
.skill-category:nth-child(1) {
    grid-column: 1 / 3;
}

.skill-category:nth-child(2) {
    grid-column: 3 / 5;
}

.skill-category:nth-child(3) {
    grid-column: 5 / 7;
}

/* Second row - center both items by positioning them in the middle columns */
.skill-category:nth-child(4) {
    grid-column: 2 / 4;
    grid-row: 2;
}

.skill-category:nth-child(5) {
    grid-column: 4 / 6;
    grid-row: 2;
}

.skill-category h3 {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    color: #2c3e50;
    text-align: center;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.skill-item {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
    transition: all 0.3s ease;
    text-align: center;
    display: inline-block;
}

.skill-item:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.experience-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    max-width: 800px;
    margin: 0 auto;
}

.stat {
    background: rgba(255, 255, 255, 0.9);
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
}

.stat h4 {
    font-size: 2.5rem;
    font-weight: 700;
    color: #667eea;
    margin-bottom: 0.5rem;
}

.stat p {
    color: #666;
    font-size: 1.1rem;
}

/* Mobile Styles */
@media (max-width: 768px) {
    .skills-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .skill-category {
        padding: 1.5rem;
    }

    .skill-category:nth-child(1),
    .skill-category:nth-child(2),
    .skill-category:nth-child(3),
    .skill-category:nth-child(4),
    .skill-category:nth-child(5) {
        grid-column: 1;
        grid-row: unset;
    }

    .experience-stats {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .stat {
        padding: 1.5rem;
    }

    .stat h4 {
        font-size: 2rem;
    }
}